// Вёрстка хедера
.header {
    height: 40px;
    background-color: #A7B1AF;
    margin-bottom: 22px;
    // .header__container

    &__container {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    // .header__text

    &__text {
        font-family: $Asap;
        color: $black-green;
        font-size: 22.59px;
        font-weight: 400;
        line-height: 33.88px;
        text-decoration: none;
    }

    // .header__button

    &__button {
        display: inline-block;
        width: 112px;
        height: 24px;
        background-color: $black-green;
        border-radius: 12px;
        display: flex;
        align-items: center;
        text-decoration: none;
        img{
            margin-left: 6px;
        }
        span{
            color: $gray;
            font-size: 12px;
            font-family: $Asap;
            font-weight: 400;
            line-height: 18px;
            margin-left: 11px;
        }
    }
}
// Верстка главного экрана, который появляется при запуске приложения
.preloader {
    display: none;
    // .preloader__active

    &__active {
        display: block;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 50;
    }


    // .preloader__img

    &__img {
        position: relative;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        padding-top: 10%;
        img{
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    // .preloader__circle

    &__circle {
        position: absolute;
        width: 48px;
        height: 48px;
        border-radius: 50%;
        left: 50%;
        top: 54%;
        background-color: $black-green;
        transform: translate(-50%, -50%);
        box-shadow: 0 0px 10px rgba(0, 0, 0, 0.8);
        z-index: 102;
        display: flex;
        align-items: center;
        justify-content: center;
        &:before{
            content: '';
            position: relative;
             width: 0;
             height: 0;
             left: 3px;
             border-top: 16px solid transparent; 
             border-bottom: 16px solid transparent; 
             border-left: 16px solid $yellow; 
        }
    }
}  
