// Блок со стрелочками (2)
.block-arrow {
    margin-bottom: 25px;
    // .block-arrow__row

    &__row {
        display: flex;
        justify-content: space-between;
        margin: 0px -5px;
    }

    &__column{
        flex: 0 1 50%;
        padding: 0px 5px;
    }

    &__img{
        background-color: #223B38;
        width: 47px;
        height: 47px;
