}
// Блок с главной фотографией (3)
.photo {
    position: relative;
    padding-top: 67%;
    margin-bottom: 30px;
    img{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
    &:after,
    &:before{
        content: '';
        position: absolute;
        height: 2px;
        width: 36px;
        background-color: $black-green;
        left: 50%;
        transform: translate(-50%, 0%);
    }
    &:before{
        top: -5px;
    }
    &:after{
        bottom: -5px;
    }
}
