// mail //////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

.mail {
    padding-top: 25px;
    padding-bottom: 25px;
    background: linear-gradient(to bottom, #314945, #96A9A0);
    // .mail__title

    &__title {
        text-align: center; color: #CCEC60; font-size: 10px; font-family: $JetBrains; font-style: italic; font-weight: 400; line-height: 20px; margin-bottom: 30px;
    }

    // .mail__form

    &__form {
        padding: 10px 20px 20px 20px;
        background: linear-gradient(to right, #B7C8C0, #7D918A);
        border-radius: 20px;
    }

    // .mail__button

    &__button {
        color: #223B38; font-size: calc(11px + (16 - 11) * (100vw - 375px) / (768 - 375)); font-family: $JetBrains; font-weight: 700; line-height: 26.47px; margin: 0px auto 40px auto; border-radius: 20px; background-color: $yellow; display: block; border: none; padding: 5px 20px;
    }

    // .mail__input

    &__input {
        display: flex;
        justify-content: space-between;
        margin-bottom: 20px;
        span{
            color: #223B38; font-size: calc(10px + (14 - 10) * (100vw - 375px) / (768 - 375)); font-family: $JetBrains; font-weight: 700; line-height: 26.47px;
        }
        input{
            width: 70%;
            border: none;
            background-color: #C9D9CD;
            border-radius: 10px;
            padding: 5px 10px;
        }
    }

    // .mail__line

    &__line {
        width: 100%;
        height: 1px;
        background-color: $black-green;
        border-radius: 1px;
        margin-bottom: 30px;
    }

    // .mail__text

    &__text {
        color: #223B38; font-size: calc(10px + (14 - 10) * (100vw - 375px) / (768 - 375)); font-family: $JetBrains; font-weight: 700; line-height: 15px; margin-bottom: 20px;padding-right: 45%;
    }

    // .mail__checkbox-container

    &__checkbox-container {
        padding-left: 30px;
        margin-bottom: 20px;
    }

    // .mail__checkbox

    &__checkbox {
        display: flex;
        align-items: center;
        input{
            margin-right: 10px;
            background-color: inherit;
            border: 1px solid $yellow;
        }
        span{
            color: #223B38; font-size: calc(9px + (14 - 9) * (100vw - 375px) / (768 - 375)); font-family: $JetBrains; font-weight: 400; line-height: 20px;
        }
    }

    &__textarea-wrapper{
        position: relative;
        display: inline-block;
        padding-left: 50px;
        margin-bottom: 30px;
        span{
            color: #223B38;
            font-size: calc(9px + (14 - 9) * (100vw - 375px) / (768 - 375));
            font-family:$JetBrains;
            font-weight: 400;
            line-height: 15px;
        }
        textarea{
            border: none;
            border-bottom: 1px solid black;
            outline: none;
            resize: none;
            height: 20px;
            width: 172px; /* Замените на нужную ширину */
            font-size: 16px; /* Замените на нужный размер шрифта */
            background-color: inherit;
        }
    }

    // .mail__subtext

    &__subtext {
        position: relative;
        color: #223B38;
        font-size: calc(10px + (14 - 10) * (100vw - 375px) / (768 - 375));
        font-family: $JetBrains;
        font-style: italic;
        font-weight: 400;
        line-height: 20px;
        text-align: center;
        left: 50%;
        transform: translate(-50%, -0%);
        margin-bottom: 20px;
        display: inline-block;
        padding-bottom: 3px;
        border-bottom: 1px solid #223B38;
    }

    // .mail__button-end

    &__button-end {
        color: #223B38;
        font-size: calc(11px + (16 - 11) * (100vw - 375px) / (768 - 375));
        font-family: $JetBrains;
        font-weight: 700;
        line-height: 26.47px;
        margin: 0px auto;
        border-radius: 20px;
        background-color: $yellow;
        display: block;
        border: none; 
        text-align: center;
        padding: 7px 0px;
        width: 100%;
    }
}


  input[type="checkbox"] {
    width: 9px;
    height: 9px;
    background-color: transparent;
    border: 1px solid $yellow;
    appearance: none;
    -webkit-appearance: none;
  }
  input[type="checkbox"]:checked{
    background: url("../img/other-pages/mail/galochka.svg") no-repeat center;
  }

  .mail__textarea-wrapper textarea:focus + span{
    transform: translateY(-100%);
    font-size: 9px; /* Размер шрифта для текста ввода */
  }














