*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
}

body {
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

// Цвета переменные
$black-green: #223B38;
$midle-black-green: #294744;
$low-black-green: #2F5954;
$yellow: #CCEC60;
$gray: #C9D9CD;
$sandy: #C09F6D;
$midle-sandy: #E2D6BD;
$low-sandy: #F8EEDA;
$black: #151515;

// Крифты
$JetBrains: "JetBrains Mono", monospace;
$Asap: "Asap", sans-serif;
$Inter: "Inter Tight", sans-serif;
$Aleo: "Aleo", serif;

// Контейнер
.__container{
	max-width: 765px;
	margin: 0 auto;
	padding: 0px 7.5px;
	overflow: hidden;
}